/*******************************************************************************
 * Styles for the base elements of the theme.
 ******************************************************************************/

// Import Google Fonts
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&family=JetBrains+Mono:wght@400;500&display=swap');

// Typography
body {
 font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
 font-size: 16px;
 line-height: 1.6;
 letter-spacing: -0.011em;
 font-weight: 400;
 -webkit-font-smoothing: antialiased;
 -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6 {
 color: var(--global-heading-color);
 font-weight: 700;
 line-height: 1.2;
 margin-bottom: 1rem;
 letter-spacing: -0.02em;
}

h1 {
 font-size: 2.5rem;
 font-weight: 800;
 background: var(--global-gradient-primary);
 -webkit-background-clip: text;
 -webkit-text-fill-color: transparent;
 background-clip: text;
}

h2 {
 font-size: 2rem;
 font-weight: 700;
}

h3 {
 font-size: 1.5rem;
 font-weight: 600;
}

p, div, li, span {
 color: var(--global-text-color);
}

strong, em {
 color: var(--global-text-color);
}

hr {
 border: none;
 height: 1px;
 background: var(--global-gradient-primary);
 opacity: 0.2;
 margin: 2rem 0;
}

table {
 td, th {
 color: var(--global-text-color);
 }
 td {
 font-size: 1rem;
 }
}

a, table.table a {
 color: var(--global-theme-color);
 text-decoration: none;
 position: relative;
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 
 &::after {
 content: '';
 position: absolute;
 bottom: -2px;
 left: 0;
 width: 0;
 height: 2px;
 background: var(--global-gradient-primary);
 transition: width 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 }
 
 &:hover {
 color: var(--global-hover-color);
 
 &::after {
 width: 100%;
 }
 }
}

figure, img {
 max-width: 90vw;
}

blockquote {
 background: linear-gradient(135deg, var(--global-bg-color-secondary), var(--global-bg-color));
 border-left: 3px solid var(--global-theme-color);
 margin: 1.5em 0;
 padding: 1rem 1.5rem;
 font-size: 1.1rem;
 border-radius: 0 8px 8px 0;
 box-shadow: var(--global-box-shadow-sm);
 font-style: italic;
 position: relative;
 
 &::before {
 content: '"';
 position: absolute;
 top: -10px;
 left: 10px;
 font-size: 3rem;
 color: var(--global-theme-color);
 opacity: 0.3;
 }
}

// Math

.equation {
 margin-bottom: 1rem;
 text-align: center;
}

// Caption

.caption {
 font-size: 0.875rem;
 margin-top: 0.75rem;
 margin-bottom: 1.5rem;
 text-align: center;
}

// Card
.card {
 background-color: var(--global-card-bg-color);
 border: 1px solid var(--global-border-color);
 border-radius: 12px;
 overflow: hidden;
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 box-shadow: var(--global-box-shadow-sm);
 
 &:hover {
 transform: translateY(-4px);
 box-shadow: var(--global-box-shadow-lg);
 background-color: var(--global-card-hover-bg);
 }

 img {
 width: 100%;
 transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 }
 
 &:hover img {
 transform: scale(1.05);
 }

 .card-title {
 color: var(--global-heading-color);
 font-weight: 600;
 }

 .card-item {
 width: auto;
 margin-bottom: 10px;

 .row {
 display: flex;
 align-items: center;
 }
 }
}

// Citation

.citation, .citation-number {
 color: var(--global-theme-color);
}

// Profile
.profile {
 margin-left: 1rem;
 width: 100%;
 
 img {
 border-radius: 12px;
 box-shadow: var(--global-box-shadow-md);
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 
 &:hover {
 transform: scale(1.02);
 box-shadow: var(--global-box-shadow-lg);
 }
 }

 .address {
 margin-top: 1rem;
 padding: 1rem;
 background: var(--global-bg-color-secondary);
 border-radius: 8px;
 font-family: 'JetBrains Mono', monospace;
 font-size: 0.9rem;
 
 p {
 display: inline-block;
 margin: 0;
 color: var(--global-text-color-light);
 }
 }
}

@media (min-width: 576px) {
 .profile {
 width: 30%;
 .address {
 p { display: block; }
 }
 }
}

.post-description {
 margin-bottom: 2rem;
 font-size: 0.875rem;
 a {
 color: inherit;
 &:hover {
 color: var(--global-theme-color);
 text-decoration: none;
 }
 }
}


// Navbar customization
.navbar {
 box-shadow: var(--global-box-shadow-sm);
 border-bottom: 1px solid var(--global-divider-color);
 background: rgba(255, 255, 255, 0.8);
 backdrop-filter: blur(10px);
 -webkit-backdrop-filter: blur(10px);
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 
 html[data-theme='dark'] & {
 background: rgba(15, 20, 25, 0.8);
 }
}
.navbar .dropdown-menu {
 background-color: var(--global-bg-color);
 border: 1px solid var(--global-divider-color);
 a:not(.active) {
 color: var(--global-text-color);
 }
 a:hover {
 color: var(--global-hover-color);
 }
 .dropdown-divider {
 border-top: 1px solid var(--global-divider-color) !important;
 }
}
.dropdown-item {
 color: var(--global-text-color);
 &:hover {
 color: var(--global-hover-color);
 background-color: var(--global-bg-color);
 }
}
.navbar.navbar-light {
 a {
 &:hover {
 text-decoration: none;
 }
 
 &::after {
 display: none;
 }
 }
 
 .navbar-brand {
 color: var(--global-text-color);
 font-weight: 700;
 font-size: 1.3rem;
 letter-spacing: -0.02em;
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 
 &:hover {
 transform: scale(1.05);
 }
 }
 
 .navbar-nav .nav-item .nav-link {
 color: var(--global-text-color);
 font-weight: 500;
 position: relative;
 padding: 0.5rem 1rem;
 margin: 0 0.25rem;
 border-radius: 8px;
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 
 &:hover {
 color: var(--global-hover-color);
 background: var(--global-bg-color-secondary);
 }
 }
 
 .navbar-nav .nav-item.active > .nav-link {
 background: var(--global-gradient-primary);
 color: white;
 font-weight: 600;
 
 &:hover {
 color: white;
 transform: scale(1.05);
 }
 }
 .navbar-brand.social {
 padding-bottom: 0;
 padding-top: 0;
 font-size: 1.5rem;

 a {
 margin: 0 0.5rem !important;
 position: relative !important;
 color: var(--global-text-color) !important;
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1) !important;


 // Override any general link styles
 &::after {
 display: none !important;
 }

 i {
 color: var(--global-text-color) !important;
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1) !important;
 }

 &:hover i {
 color: var(--global-theme-color) !important;
 transform: translateY(-3px) !important;
 }
 }
 }
}

.navbar-toggler {
 .icon-bar {
 display: block;
 width: 22px;
 height: 2px;
 background-color: var(--global-text-color);
 border-radius: 1px;
 margin-bottom: 4px;
 transition: all 0.2s;
 }
 .top-bar {
 transform: rotate(45deg);
 transform-origin: 10% 10%;
 }
 .middle-bar {
 opacity: 0;
 }
 .bottom-bar {
 transform: rotate(-45deg);
 transform-origin: 10% 90%;
 }
}

.navbar-toggler.collapsed {
 .top-bar {
 transform: rotate(0);
 }
 .middle-bar {
 opacity: 1;
 }
 .bottom-bar {
 transform: rotate(0);
 }
}

// Dark mode toggle button
.toggle-container {
 display: flex;
 align-items: center;
 margin-left: 1rem;
}

#light-toggle {
 padding: 0.5rem;
 border: 2px solid var(--global-border-color);
 background-color: var(--global-bg-color-secondary);
 color: var(--global-text-color);
 border-radius: 8px;
 cursor: pointer;
 font-size: 1.2rem;
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 display: flex;
 align-items: center;
 justify-content: center;
 width: 44px;
 height: 44px;
 
 &:hover {
 color: var(--global-hover-color);
 background: var(--global-gradient-primary);
 border-color: transparent;
 transform: scale(1.05);
 
 i {
 color: white !important;
 }
 }
 
 &:focus {
 outline: none;
 box-shadow: 0 0 0 3px rgba(108, 99, 255, 0.25);
 }
 
 i {
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 font-size: 1.1rem;
 }
}

// Social (bottom)

.social {
 text-align: center;
 padding: 2rem 0;
 
 .contact-icons {
 font-size: 2.5rem;
 display: flex;
 justify-content: center;
 gap: 2rem;
 
 a {
 position: relative;
 display: inline-block;
 padding: 0.75rem;
 background: var(--global-bg-color-secondary);
 border-radius: 12px;
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 
 i::before {
 color: var(--global-text-color);
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 }
 
 &:hover {
 transform: translateY(-5px) scale(1.1);
 background: var(--global-gradient-primary);
 box-shadow: var(--global-box-shadow-lg);
 
 i::before {
 color: white;
 }
 }
 
 &::after {
 display: none;
 }
 }
 }
 
 .contact-note {
 font-size: 0.9rem;
 margin-top: 1.5rem;
 color: var(--global-text-color-light);
 }
}


// Footer
footer.fixed-bottom {
 background: var(--global-footer-bg-color);
 font-size: 0.85rem;
 border-top: 1px solid var(--global-divider-color);
 
 .container {
 color: var(--global-footer-text-color);
 padding-top: 12px;
 padding-bottom: 12px;
 }
 
 a {
 color: var(--global-footer-link-color);
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 
 &::after {
 display: none;
 }
 
 &:hover {
 color: var(--global-theme-color);
 text-decoration: none;
 }
 }
}

footer.sticky-bottom {
 border-top: 1px solid var(--global-divider-color);
 padding-top: 40px;
 padding-bottom: 40px;
 font-size: 0.9rem;
}

// CV

.cv {
 margin-bottom: 40px;
 
 .card {
 background-color: var(--global-card-bg-color);
 border: 1px solid var(--global-divider-color);
 
 .list-group-item {
 background-color: inherit;

 .badge {
 color: var(--global-card-bg-color) !important;
 background-color: var(--global-theme-color) !important;
 }
 }
 }
}

// Blog

.header-bar {
 border-bottom: 1px solid var(--global-divider-color);
 text-align: center;
 padding-top: 2rem;
 padding-bottom: 5rem;
 h1 {
 color: var(--global-theme-color);
 font-size: 5rem;
 }
}

.post-list {
 margin: 0;
 margin-bottom: 40px;
 padding: 0;
 li {
 border-bottom: 1px solid var(--global-divider-color);
 list-style: none;
 padding-top: 2rem;
 padding-bottom: 2rem;
 
 .post-header {
 display: flex;
 align-items: flex-start;
 gap: 1rem;
 margin-bottom: 0.75rem;
 
 .post-icon {
 flex-shrink: 0;
 display: flex;
 align-items: center;
 justify-content: center;
 width: 120px;
 height: 120px;
 background: var(--global-bg-color-secondary);
 border-radius: 12px;
 border: 1px solid var(--global-border-color);
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 box-shadow: var(--global-box-shadow-sm);
 margin-top: 0.125rem;
 
 i {
 font-size: 2rem;
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 }
 
 &:hover {
 transform: translateY(-2px) scale(1.05);
 box-shadow: var(--global-box-shadow-md);
 background: var(--global-card-hover-bg);
 
 i {
 transform: scale(1.1);
 }
 }
 }
 
 h3 {
 flex: 1;
 margin: 0;
 line-height: 1.3;
 
 .post-title {
 font-size: 1.4rem;
 font-weight: 600;
 color: var(--global-heading-color);
 text-decoration: none;
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 
 &::after {
 display: none;
 }
 
 &:hover {
 color: var(--global-theme-color);
 transform: translateX(5px);
 }
 }
 }
 }
 
 .post-meta {
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 margin-bottom: 0;
 margin-left: 80px; // Align with title text (icon width + gap)
 }
 .post-tags {
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 padding-top: 0.25rem;
 padding-bottom: 0;
 margin-left: 80px; // Align with title text (icon width + gap)
 }
 
 p {
 margin-left: 80px; // Align description with title text
 margin-bottom: 0.5rem;
 }
 
 a {
 color: var(--global-text-color);
 text-decoration: none;
 &:hover {
 color: var(--global-theme-color);
 }
 }
 }
}

// Responsive adjustments for smaller screens
@media (max-width: 768px) {
 .post-list {
 li {
 .post-header {
 gap: 0.75rem;
 
 .post-icon {
 width: 96px;
 height: 96px;
 
 i {
 font-size: 1.75rem;
 }
 }
 
 h3 .post-title {
 font-size: 1.2rem;
 }
 }
 
 .post-meta,
 .post-tags,
 p {
 margin-left: 68px; // Adjusted for smaller icon
 }
 }
 }
}

@media (max-width: 480px) {
 .post-list {
 li {
 .post-header {
 gap: 0.5rem;
 
 .post-icon {
 width: 80px;
 height: 80px;
 
 i {
 font-size: 1.4rem;
 }
 }
 
 h3 .post-title {
 font-size: 1.1rem;
 }
 }
 
 .post-meta,
 .post-tags,
 p {
 margin-left: 60px; // Adjusted for smallest icon
 }
 }
 }
}

.pagination {
 .page-item {
 .page-link {
 color: var(--global-text-color);
 &:hover {
 color: $black-color;
 }
 }
 &.active .page-link {
 color: $white-color;
 background-color: var(--global-theme-color);
 &:hover {
 background-color: var(--global-theme-color);
 }
 }
 }
}


// Distill

.distill {
 a:hover {
 border-bottom-color: var(--global-theme-color);
 text-decoration: none;
 }
}


// Projects

.projects {
 a {
 text-decoration: none;
 
 &::after {
 display: none;
 }

 &:hover {
 .card-title {
 background: var(--global-gradient-primary);
 -webkit-background-clip: text;
 -webkit-text-fill-color: transparent;
 background-clip: text;
 }
 }
 }

 .card {
 height: 100%;
 display: flex;
 flex-direction: column;
 
 img {
 width: 100%;
 height: 200px;
 object-fit: cover;
 }
 
 .card-body {
 flex: 1;
 padding: 1.5rem;
 }
 }

 .card-item {
 width: auto;
 margin-bottom: 2rem;

 .row {
 display: flex;
 align-items: center;
 }
 }

 .grid-sizer, .grid-item {
 width: 250px;
 margin-bottom: 10px;
 }

 h2.category {
 color: var(--global-divider-color);
 border-bottom: 1px solid var(--global-divider-color);
 padding-top: 0.5rem;
 margin-top: 2rem;
 margin-bottom: 1rem;
 text-align: right;
 }
}


// Publications

.publications {
 margin-top: 2rem;
 
 h1 {
 font-size: 2.5rem;
 text-align: center;
 margin-top: 1em;
 margin-bottom: 1em;
 background: var(--global-gradient-primary);
 -webkit-background-clip: text;
 -webkit-text-fill-color: transparent;
 background-clip: text;
 }
 h2 {
 margin-bottom: 1rem;
 span {
 font-size: 1.5rem;
 }
 }
 h2.year {
 color: var(--global-divider-color);
 border-top: 1px solid var(--global-divider-color);
 padding-top: 1rem;
 margin-top: 2rem;
 margin-bottom: -2rem;
 text-align: right;
 }
 ol.bibliography {
 list-style: none;
 padding: 0;
 margin-top: 0;

 li {
 margin-bottom: 2rem;
 padding: 1.5rem;
 background: var(--global-bg-color-secondary);
 border-radius: 12px;
 border: 1px solid var(--global-border-color);
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 
 &:hover {
 box-shadow: var(--global-box-shadow-md);
 transform: translateX(5px);
 }
 
 .abbr {
 height: 2rem;
 margin-bottom: 0.75rem;
 
 abbr {
 display: inline-block;
 background: var(--global-gradient-primary);
 padding: 0.25rem 1rem;
 border-radius: 20px;
 font-weight: 600;
 
 a {
 color: white;
 
 &::after {
 display: none;
 }
 
 &:hover {
 text-decoration: none;
 }
 }
 }
 .award {
 color: var(--global-theme-color) !important;
 border: 1px solid var(--global-theme-color);
 }
 }
 .title {
 font-weight: bolder;
 }
 .author {
 a {
 border-bottom: 1px dashed var(--global-theme-color);
 &:hover {
 border-bottom-style: solid;
 text-decoration: none;
 }
 }
 > em {
 border-bottom: 1px solid;
 font-style: normal;
 }
 }
 .links {
 a.btn {
 color: var(--global-text-color);
 border: 2px solid var(--global-border-color);
 background: var(--global-bg-color);
 padding: 0.5rem 1rem;
 border-radius: 8px;
 font-weight: 500;
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 margin-right: 0.5rem;
 
 &::after {
 display: none;
 }
 
 &:hover {
 color: white;
 background: var(--global-gradient-primary);
 border-color: transparent;
 transform: translateY(-2px);
 box-shadow: var(--global-box-shadow-md);
 }
 }
 }
 .hidden {
 font-size: 0.875rem;
 max-height: 0px;
 overflow: hidden;
 text-align: justify;
 transition-property: 0.15s ease;
 -moz-transition: 0.15s ease;
 -ms-transition: 0.15s ease;
 -o-transition: 0.15s ease;
 transition: all 0.15s ease;

 p {
 line-height: 1.4em;
 margin: 10px;
 }
 pre {
 font-size: 1em;
 line-height: 1.4em;
 padding: 10px;
 }
 }
 .hidden.open {
 max-height: 100em;
 transition-property: 0.15s ease;
 -moz-transition: 0.15s ease;
 -ms-transition: 0.15s ease;
 -o-transition: 0.15s ease;
 transition: all 0.15s ease;
 }
 div.abstract.hidden {
 border: dashed 1px var(--global-bg-color);
 }
 div.abstract.hidden.open {
 border-color: var(--global-text-color);
 }
 }
 }
}

// Rouge Color Customization
figure.highlight {
 margin: 0 0 1rem;
}

pre {
 color: var(--global-theme-color);
 background-color: var(--global-code-bg-color);
 border-radius: 8px;
 padding: 1rem;
 border: 1px solid var(--global-border-color);
 font-family: 'JetBrains Mono', monospace;
 font-size: 0.9rem;
 line-height: 1.5;
 
 pre, code {
 background-color: transparent;
 border-radius: 0;
 padding: 0;
 }
}

code {
 color: var(--global-theme-color);
 background-color: var(--global-code-bg-color);
 border-radius: 4px;
 padding: 0.2rem 0.4rem;
 font-family: 'JetBrains Mono', monospace;
 font-size: 0.875em;
 font-weight: 500;
}


// Transitioning Themes with smooth animation
html.transition,
html.transition *,
html.transition *:before,
html.transition *:after {
 transition: all 500ms cubic-bezier(0.4, 0, 0.2, 1) !important;
 transition-delay: 0 !important;
}

// Animation keyframes
@keyframes fadeIn {
 from {
 opacity: 0;
 transform: translateY(20px);
 }
 to {
 opacity: 1;
 transform: translateY(0);
 }
}

@keyframes slideIn {
 from {
 opacity: 0;
 transform: translateX(-20px);
 }
 to {
 opacity: 1;
 transform: translateX(0);
 }
}

// Apply animations
.profile, .news, .publications, .projects {
 animation: fadeIn 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.navbar {
 animation: slideIn 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

// Improved scrollbar
::-webkit-scrollbar {
 width: 10px;
 height: 10px;
}

::-webkit-scrollbar-track {
 background: var(--global-bg-color-secondary);
}

::-webkit-scrollbar-thumb {
 background: var(--global-theme-color);
 border-radius: 5px;
 
 &:hover {
 background: var(--global-theme-color-dark);
 }
}

// Extra Markdown style (post Customization)
.post{
 .post-meta{
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 margin-bottom: 0;
 }
 .post-tags{
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 padding-top: 0.25rem;
 padding-bottom: 1rem;
 a {
 color: var(--global-text-color-light);
 text-decoration: none;
 &:hover {
 color: var(--global-theme-color);
 }
 }
 }
 .post-content{
 blockquote {
 border-left: 5px solid var(--global-theme-color);
 padding: 8px;
 }
 }
}
