/*******************************************************************************
 * Custom Unique Effects - Performance Optimized
 ******************************************************************************/

// Floating shapes disabled for performance - keeping styles for potential future use
.floating-shapes {
 display: none; // Disabled for faster page loading
}

// Glowing text effect for name - simplified
h1 {
 position: relative;
 
 &::after {
 content: attr(data-text);
 position: absolute;
 left: 0;
 top: 0;
 z-index: -1;
 background: var(--global-theme-color);
 -webkit-background-clip: text;
 -webkit-text-fill-color: transparent;
 background-clip: text;
 filter: blur(8px);
 opacity: 0;
 animation: glow 4s ease-in-out infinite;
 }
}

@keyframes glow {
 0%, 100% { opacity: 0; }
 50% { opacity: 0.1; } // Reduced opacity for subtlety
}

// Typing effect for main description
.typing-effect {
 overflow: hidden;
 border-right: 3px solid var(--global-theme-color);
 white-space: nowrap;
 animation: typing 3.5s steps(40, end), blink-caret 0.75s step-end infinite;
 max-width: fit-content;
}

@keyframes typing {
 from { width: 0; }
 to { width: 100%; }
}

@keyframes blink-caret {
 from, to { border-color: transparent; }
 50% { border-color: var(--global-theme-color); }
}

// Enhanced Card Design - professional and engaging
.card {
 transform-style: preserve-3d;
 transition: all 0.3s ease;
 border-radius: 12px;
 box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
 border: 1px solid rgba(255, 255, 255, 0.1);
 backdrop-filter: blur(10px);
 
 @media (prefers-reduced-motion: no-preference) {
 &:hover {
 transform: translateY(-8px) scale(1.02);
 box-shadow: 0 12px 40px rgba(0, 0, 0, 0.15);
 }
 }
 
 &::before {
 content: '';
 position: absolute;
 top: 0;
 left: 0;
 width: 100%;
 height: 100%;
 background: linear-gradient(135deg, transparent 40%, rgba(255, 255, 255, 0.05) 50%, transparent 60%);
 transform: translateX(-100%);
 transition: transform 0.4s ease;
 pointer-events: none;
 border-radius: inherit;
 }
 
 @media (prefers-reduced-motion: no-preference) {
 &:hover::before {
 transform: translateX(100%);
 }
 }
}

// Professional button design
.btn, button {
 position: relative;
 z-index: 1;
 border-radius: 8px;
 font-weight: 500;
 letter-spacing: 0.5px;
 transition: all 0.3s ease;
 border: 1px solid transparent;
 
 &:not(.btn-sm) {
 padding: 0.75rem 1.5rem;
 font-size: 0.95rem;
 }
 
 &.btn-outline-primary {
 border-color: var(--global-theme-color);
 background: rgba(var(--global-theme-color-rgb), 0.05);
 
 @media (prefers-reduced-motion: no-preference) {
 &:hover {
 background: var(--global-theme-color);
 transform: translateY(-2px);
 box-shadow: 0 4px 15px rgba(var(--global-theme-color-rgb), 0.3);
 }
 }
 }
 
 // Subtle glow effect for primary buttons
 &.btn-primary {
 background: linear-gradient(135deg, var(--global-theme-color), var(--global-secondary-color));
 border: none;
 
 @media (prefers-reduced-motion: no-preference) {
 &:hover {
 transform: translateY(-2px);
 box-shadow: 0 8px 25px rgba(var(--global-theme-color-rgb), 0.4);
 }
 }
 }
}

// Enhanced navigation design
.nav-link {
 position: relative;
 transition: all 0.3s ease;
 font-weight: 500;
 letter-spacing: 0.3px;
 border-radius: 6px;
 padding: 0.5rem 1rem !important;
 
 &::after {
 content: '';
 position: absolute;
 bottom: 0;
 left: 50%;
 width: 0;
 height: 2px;
 background: linear-gradient(90deg, var(--global-theme-color), var(--global-secondary-color));
 transition: all 0.3s ease;
 transform: translateX(-50%);
 border-radius: 1px;
 }
 
 @media (prefers-reduced-motion: no-preference) {
 &:hover {
 background: rgba(var(--global-theme-color-rgb), 0.08);
 transform: translateY(-1px);
 }
 
 &:hover::after,
 &.active::after {
 width: 80%;
 }
 }
 
 // Static state for reduced motion
 @media (prefers-reduced-motion: reduce) {
 &:hover,
 &.active {
 background: rgba(var(--global-theme-color-rgb), 0.1);
 }
 
 &.active::after {
 width: 80%;
 }
 }
}

// Scroll progress indicator
.scroll-progress {
 position: fixed;
 top: 0;
 left: 0;
 height: 5px;
 background: linear-gradient(90deg, var(--global-theme-color), var(--global-secondary-color));
 z-index: 9999;
 will-change: width;
}

// Parallax effect for profile image - simplified
.profile img {
 transition: transform 0.2s ease-out, box-shadow 0.2s ease;
 
 &:hover {
 transform: scale(1.05);
 box-shadow: 0 10px 30px rgba(108, 99, 255, 0.2);
 }
}

// Animated underline with gradient - simplified
// Exclude navbar social icons from this effect
a:not(.navbar-brand.social a) {
 position: relative;
 
 &::after {
 content: '';
 position: absolute;
 bottom: 0;
 left: 0;
 width: 0;
 height: 2px;
 background: var(--global-theme-color);
 transition: width 0.2s ease;
 }
 
 &:hover::after {
 width: 100%;
 }
}

// Ensure navbar social links are not affected by general link styles
.navbar-brand.social a {
 // Reset any inherited styles that might interfere
 &::after {
 display: none !important;
 }
}

// Mouse trail effect - simplified
.mouse-trail {
 position: fixed;
 width: 20px;
 height: 20px;
 border-radius: 50%;
 background: var(--global-theme-color);
 pointer-events: none;
 opacity: 0.3;
 transform: translate(-50%, -50%);
 transition: opacity 0.2s;
 z-index: 9998;
 
 &.hide {
 opacity: 0;
 }
}

// Subtle static background accents - reduced opacity
.background-accent {
 position: fixed;
 border-radius: 50%;
 filter: blur(80px);
 opacity: 0.02;
 pointer-events: none;
 z-index: -1;
 
 &.accent1 {
 top: 10%;
 left: 10%;
 width: 200px;
 height: 200px;
 background: var(--global-theme-color);
 }
 
 &.accent2 {
 bottom: 10%;
 right: 10%;
 width: 300px;
 height: 300px;
 background: var(--global-secondary-color);
 }
}

// Rainbow text animation - optimized
.rainbow-text {
 background: linear-gradient(90deg, #6C63FF, #4ECDC4, #FF6B6B, #6C63FF);
 background-size: 200% auto;
 -webkit-background-clip: text;
 -webkit-text-fill-color: transparent;
 background-clip: text;
 animation: rainbow 4s linear infinite;
}

@keyframes rainbow {
 to { background-position: 200% center; }
}

// Particle effect disabled for performance
.particles {
 display: none; // Disabled for faster page loading
}

// Magnetic button effect - simplified
.magnetic-btn {
 transition: transform 0.15s ease;
 
 &:hover {
 transform: scale(1.05);
 }
}

// Liquid fill effect - optimized
.liquid-fill:not(.btn-sm) {
 position: relative;
 overflow: hidden;
 
 &::before {
 content: '';
 position: absolute;
 top: 100%;
 left: 0;
 width: 100%;
 height: 100%;
 background: var(--global-theme-color);
 opacity: 0.1;
 transition: top 0.3s ease;
 z-index: -1;
 }
 
 &:hover::before {
 top: 0;
 }
}

// Ensure publication buttons work properly
.publications .links a.btn {
 pointer-events: auto !important;
 z-index: 10;
 position: relative;
 
 &:not(:hover) {
 transform: none !important;
 }
}

// Reveal animation on scroll - optimized for performance
.reveal {
 opacity: 0;
 transform: translateY(10px); // Reduced distance for faster animation
 
 &.active {
 opacity: 1;
 transform: translateY(0);
 transition: opacity 0.4s ease, transform 0.4s ease; // Faster transition
 }
}

// Glitch effect for headers - simplified
.glitch {
 position: relative;
 
 &::before,
 &::after {
 content: attr(data-text);
 position: absolute;
 top: 0;
 left: 0;
 width: 100%;
 height: 100%;
 opacity: 0;
 }
 
 &:hover::before {
 animation: glitch-1 0.5s 1;
 color: #FF6B6B;
 z-index: -1;
 opacity: 0.5;
 }
 
 &:hover::after {
 animation: glitch-2 0.5s 1;
 color: #4ECDC4;
 z-index: -2;
 opacity: 0.5;
 }
}

@keyframes glitch-1 {
 0%, 100% {
 transform: translate(0);
 }
 50% {
 transform: translate(-2px, 1px);
 }
}

@keyframes glitch-2 {
 0%, 100% {
 transform: translate(0);
 }
 50% {
 transform: translate(2px, -1px);
 }
}

// Spotlight effect - optimized
.spotlight {
 position: relative;
 overflow: hidden;
 
 &::after {
 content: '';
 position: absolute;
 top: 50%;
 left: 50%;
 width: 0;
 height: 0;
 border-radius: 50%;
 background: radial-gradient(circle, rgba(255,255,255,0.2) 0%, transparent 60%);
 transform: translate(-50%, -50%);
 transition: width 0.4s ease, height 0.4s ease;
 pointer-events: none;
 }
 
 &:hover::after {
 width: 200px;
 height: 200px;
 }
}

// Fun interaction animations
@keyframes slideInRight {
 from {
 transform: translateX(100%);
 opacity: 0;
 }
 to {
 transform: translateX(0);
 opacity: 1;
 }
}

@keyframes slideOutRight {
 from {
 transform: translateX(0);
 opacity: 1;
 }
 to {
 transform: translateX(100%);
 opacity: 0;
 }
}

@keyframes spin {
 from { transform: rotate(0deg); }
 to { transform: rotate(360deg); }
}

@keyframes bounce {
 0%, 20%, 53%, 80%, 100% {
 transform: translateY(0);
 }
 40%, 43% {
 transform: translateY(-10px);
 }
 70% {
 transform: translateY(-5px);
 }
}

@keyframes shake {
 0%, 100% { transform: translateX(0); }
 10%, 30%, 50%, 70%, 90% { transform: translateX(-2px); }
 20%, 40%, 60%, 80% { transform: translateX(2px); }
}

// Interactive profile image enhancements
.profile img {
 cursor: pointer;
 transition: all 0.3s ease;
 
 &:hover {
 transform: scale(1.05);
 filter: brightness(1.1);
 }
 
 &:active {
 transform: scale(0.98);
 }
}

// Fun title interactions
h1 {
 transition: transform 0.2s ease;
 cursor: default;
 
 &:hover {
 text-shadow: 0 0 10px rgba(var(--global-theme-color-rgb), 0.3);
 }
}

// Easter egg button styles
.easter-egg-button {
 background: linear-gradient(45deg, #ff6b6b, #4ecdc4, #45b7d1, #96ceb4);
 background-size: 400% 400%;
 animation: gradient-shift 3s ease infinite;
 border: none;
 color: white;
 padding: 10px 20px;
 border-radius: 25px;
 cursor: pointer;
 transition: transform 0.2s ease;
 
 &:hover {
 transform: scale(1.05);
 animation-duration: 1s;
 }
 
 &:active {
 transform: scale(0.95);
 }
}

@keyframes gradient-shift {
 0% { background-position: 0% 50%; }
 50% { background-position: 100% 50%; }
 100% { background-position: 0% 50%; }
}

// Click burst effect styles
.click-burst {
 position: fixed;
 width: 6px;
 height: 6px;
 border-radius: 50%;
 pointer-events: none;
 z-index: 9999;
 animation: burst 0.6s ease-out forwards;
}

@keyframes burst {
 0% {
 transform: scale(1);
 opacity: 1;
 }
 100% {
 transform: scale(3);
 opacity: 0;
 }
}

// Subtle hover effects for cards - title animation removed
.card {
 // Card hover effects without title animation
}

// Fun loading states
.loading-dots {
 &::after {
 content: '';
 animation: loading-dots 1.5s infinite;
 }
}

@keyframes loading-dots {
 0%, 20% { content: '.'; }
 40% { content: '..'; }
 60%, 100% { content: '...'; }
}

// Secret developer mode styles
.developer-mode {
 position: fixed;
 top: 10px;
 left: 10px;
 background: rgba(0, 0, 0, 0.8);
 color: #00ff00;
 padding: 5px 10px;
 font-family: 'Courier New', monospace;
 font-size: 12px;
 border-radius: 3px;
 z-index: 10001;
 
 &::before {
 content: '> ';
 color: #ffff00;
 }
}

// Performance optimization - disable animations for prefers-reduced-motion
@media (prefers-reduced-motion: reduce) {
 // Disable fun animations for users who prefer reduced motion
 .click-burst,
 .easter-egg-button,
 h1:hover,
 .profile img:hover {
 animation: none !important;
 transform: none !important;
 transition: none !important;
 }
 
 // Keep basic functionality but remove motion
 *,
 *::before,
 *::after {
 animation-duration: 0.01ms !important;
 animation-iteration-count: 1 !important;
 transition-duration: 0.01ms !important;
 animation-delay: 0.01ms !important;
 }
}
